<template>
<div class="container">
  <h3 class="center">Добавление правила в базу правил</h3>
  <div class="container-inner" style="margin-top: 150px;">
  <div class="select-wrapper">
   <div class="input-field">
    <select class="browser-default" v-model="disciplineLevel">
      <option value="низкий">низкий</option>
      <option value="ниже среднего">ниже среднего</option>
      <option value="средний">средний</option>
      <option value="выше среднего">выше среднего</option>
      <option value="высокий">высокий</option>
    </select>
    <label style="transform: translateY(-20px);">Уровень усвоения дисциплины</label>
  </div>
    <div class="input-field">
    <select class="browser-default" v-model="selfDevelopement">
      <option value="низкий">низкий</option>
      <option value="ниже среднего">ниже среднего</option>
      <option value="средний">средний</option>
      <option value="выше среднего">выше среднего</option>
      <option value="высокий">высокий</option>
    </select>
    <label style="transform: translateY(-20px);">Стремление к саморазвитию</label>
  </div>
    <div class="input-field">
    <select class="browser-default" v-model="responsibility">
      <option value="низкий">низкий</option>
      <option value="ниже среднего">ниже среднего</option>
      <option value="средний">средний</option>
      <option value="выше среднего">выше среднего</option>
      <option value="высокий">высокий</option>
    </select>
    <label style="transform: translateY(-20px);">Ответственность</label>
  </div>
    <div class="input-field">
    <select class="browser-default" v-model="perseverance">
      <option value="низкий">низкий</option>
      <option value="ниже среднего">ниже среднего</option>
      <option value="средний">средний</option>
      <option value="выше среднего">выше среднего</option>
      <option value="высокий">высокий</option>
    </select>
    <label style="transform: translateY(-20px);">Усидчивость</label>
  </div>
    <div class="input-field">
    <select class="browser-default" v-model="attentiveness">
      <option value="низкий">низкий</option>
      <option value="ниже среднего">ниже среднего</option>
      <option value="средний">средний</option>
      <option value="выше среднего">выше среднего</option>
      <option value="высокий">высокий</option>
    </select>
    <label style="transform: translateY(-20px);">Внимательность</label>
  </div>
  <div class="input-field">
    <select class="browser-default" v-model="stress">
      <option value="низкий">низкий</option>
      <option value="ниже среднего">ниже среднего</option>
      <option value="средний">средний</option>
      <option value="выше среднего">выше среднего</option>
      <option value="высокий">высокий</option>
    </select>
    <label style="transform: translateY(-20px);">Стрессоустойчивость</label>
  </div>
  </div>
  <div class="result">
    <div class="input-field">
    <select class="browser-default" v-model="result">
      <option value="новичок">новичок</option>
      <option value="стажер">стажер</option>
      <option value="мастер">мастер</option>
      <option value="профессионал">профессионал</option>
      <option value="эксперт">эксперт</option>
    </select>
    <label style="transform: translateY(-20px);">Статус обучаемого</label>
  </div>
  <button @click="submitRule" class="waves-effect waves-light btn">Отправить</button>
  </div>
  </div>

</div>
</template>

<script>
import { onMounted } from '@vue/runtime-core'
import { ref } from '@vue/runtime-core'
import axios from 'axios'

let server = "http://db.19ivt.ru:3000/"

export default {
  setup() {
    const disciplineLevel = ref('')
    const selfDevelopement = ref('')
    const responsibility = ref('')
    const perseverance = ref('')
    const attentiveness = ref('')
    const stress = ref('')
    const result = ref('')

    const submitRule = () => {
      if(disciplineLevel.value != '' && selfDevelopement.value != '' && responsibility.value != '' && perseverance.value != '' && attentiveness.value != '' && stress.value != '' && result.value != '') {
        const rule = { //отправить это
          disciplineLevel: disciplineLevel.value,
          selfDevelopment: selfDevelopement.value,
          responsibility: responsibility.value,
          perseverance: perseverance.value,
          attentiveness: attentiveness.value,
          stress: stress.value,
          result: result.value
        }
        axios.get(server + 'postRule', {params:rule});
        M.toast({html: 'Отправлено'})
          disciplineLevel.value = '';
          selfDevelopement.value = '';
          responsibility.value = '';
          perseverance.value = '';
          attentiveness.value = '';
          stress.value = '';
          result.value = '';
        console.log(rule);
      }
      else { M.toast({html: 'Вы не выбрали какое то условие'}) }
    }

    return {
      disciplineLevel,
      selfDevelopement,
      responsibility,
      perseverance,
      attentiveness,
      stress,
      result,
      submitRule
    }

    onMounted(() => {
      M.AutoInit()
    })
  },
}
</script>

<style lang="scss">
.select-wrapper{
  display: flex;
  flex-direction: column;
}
.container-inner{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

select {
  min-width: 250px;
}

@media screen and (max-width: 510px) {
  .container-inner {
    flex-direction: column;
  }
}
</style>
